// STEP 1 React Hooks
import { useState, useEffect, useCallback } from 'react';

// fetchCurrentWeather  locationName 
const fetchCurrentWeather = (locationName) => {
  // API  locationName 
  return fetch(
    `https://opendata.cwb.gov.tw/api/v1/rest/datastore/O-A0001-001?Authorization=CWB-3F00350C-B8F7-4EB4-BE76-06BB0F9C5757&locationName=${locationName}`,
  )
    .then((res) => res.json())
    .then((data) => {
      console.log(data);
      //  `locationData` 
      const locationData = data.records.location[0];

      // WDSDTEMPHUMD
      const weatherElements = locationData.weatherElement.reduce(
        (neededElements, item) => {
          // console.log(neededElements);
          if (['WDSD', 'TEMP', 'HUMD'].includes(item.elementName)) {
            neededElements[item.elementName] = item.elementValue;
          }
          return neededElements;
        },
        {},
      );

      // // STEP 3 React 
      // setWeatherElement((prevState) => ({
      //   ...prevState,
      //   observationTime: locationData.time.obsTime,
      //   locationName: locationData.locationName,
      //   description: '',
      //   temperature: weatherElements.TEMP,
      //   windSpeed: weatherElements.WDSD,
      //   humid: weatherElements.HUMD,
      // }));

      // STEP 3-2 setWeatherElement

      return {
        observationTime: locationData.time.obsTime,
        locationName: locationData.locationName,
        temperature: weatherElements.TEMP,
        windSpeed: weatherElements.WDSD,
        humid: weatherElements.HUMD,
      };
    });
};

// fetchWeatherForecast  cityName 
const fetchWeatherForecast = (cityName) => {
  return fetch(
    `https://opendata.cwb.gov.tw/api/v1/rest/datastore/F-C0032-001?Authorization=CWB-3F00350C-B8F7-4EB4-BE76-06BB0F9C5757&locationName=${cityName}`,
  )
    .then((response) => response.json())
    .then((data) => {
      console.log('data', data);
      const locationData = data.records.location[0];
      const weatherElements = locationData.weatherElement.reduce(
        (neededElements, item) => {
          // console.log(neededElements);
          if (['Wx', 'PoP', 'CI'].includes(item.elementName)) {
            neededElements[item.elementName] = item.time[0].parameter;
          }
          return neededElements;
        },
        {},
      );
      // setWeatherElement((prevState) => ({
      //   ...prevState,
      //   description: weatherElements.Wx.parameterName,
      //   weatherCode: weatherElements.Wx.parameterValue,
      //   rainPossibility: weatherElements.PoP.parameterName,
      //   comfortability: weatherElements.CI.parameterName,
      // }));
      // STEP 4-2 setWeatherElement
      return {
        description: weatherElements.Wx.parameterName,
        weatherCode: weatherElements.Wx.parameterValue,
        rainPossibility: weatherElements.PoP.parameterName,
        comfortability: weatherElements.CI.parameterName,
      };
    });
};
// useWeatherApi 
const useWeatherApi = (currentLocation) => {
  //  currentLocation  locationName  cityName
  const { locationName, cityName } = currentLocation;

  //  useState 
  const [weatherElement, setWeatherElement] = useState({
    observationTime: new Date(),
    locationName: '',
    humid: 0,
    temperature: 0,
    windSpeed: 0,
    description: '',
    weatherCode: 0,
    rainPossibility: 0,
    comfortability: '',
    isLoading: true,
  });

  //  useCallback 
  const fetchData = useCallback(() => {
    const fetchingData = async () => {
      //  Promise.all  await  API 
      // 
      const [currentWeather, weatherForecast] = await Promise.all([
        //locationName  API 
        fetchCurrentWeather(locationName),
        //cityName  API 
        fetchWeatherForecast(cityName),
      ]);
      // 
      setWeatherElement({
        ...currentWeather,
        ...weatherForecast,
        isLoading: false,
      });
    };
    //Loading
    setWeatherElement((prevState) => ({
      ...prevState,
      isLoading: true,
    }));
    //  fetchingData 
    fetchingData();
    //  fetchingData  React  dependencies 
    // locationName  cityName  useCallback  dependencies 
  }, [locationName, cityName]);

  //  useEffect 
  // // STEP 2 useEffect Hook
  // useEffect(() => {
  //   fetchCurrentWeather();
  //   fetchWeatherForecast();
  // }, []);

  //  locationName  cityName fetchData  useEffect 
  useEffect(() => {
    // STEP 5 fetchData 
    fetchData();
  }, [fetchData]);

  // STEP 5 React 
  return [weatherElement, fetchData];
};

export default useWeatherApi;
